import axios from 'axios'
// 引入服务器错误提示
import ErrorMessage from '@/common/utils/errorMessage'
// vuex存储
import store from '@/data/store'
// 引入moment
import moment from 'moment'
const w = window

// 自定义配置创建axios的新实例
let base = 'api'
const service = axios.create({
  baseURL: base,
  timeout: 60000
})
// request拦截器
service.interceptors.request.use(config => {
  // localStorage.setItem("token", 'eyJhbGciOiJIUzUxMiJ9.eyJzdWIiOiIxODg3NDgwNzkyNyIsImNyZWF0ZWQiOjE2MzEyNjA0ODc5NDUsImV4cCI6MTYzMTg2NTI4N30._WwWideViy9ZvijpSwOtxJR2oV2zSDgdfMm7ctapnrc-dYB8-R6F63krtwnMe7uRhyHtlD9OmOyNYHRCOoGsaA')
  config.headers['Authorization'] = 'Bearer ' + localStorage.getItem('token')
  
  return config
}, error => {
  Promise.reject(error)
})
// respone拦截器
service.interceptors.response.use(
  response => {
    if (response.data.code !== 200) {
      if (response.data.code === 401) {
        localStorage.setItem('code','401')
        if (localStorage.getItem('token')) {
          window.localStorage.removeItem('token')
        }
        const { userAgent } = navigator
        let isWeixin = !!/MicroMessenger/i.test(userAgent)
        if (isWeixin) {
          let st = {
            name: 'home',
            url: '/user/home/index'
          }
          window.location.href="https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx372e6d05b22ce032&redirect_uri=" + process.env.wxUrl + "&response_type=code&scope=snsapi_base&state=" + JSON.stringify(st) + "#wechat_redirect"
        } else {
          window.location.href = window.location.origin + '/user/login/index'
        }
        response.data.message = '请重新登录'
      } else {
        store.dispatch('AddErrorLog', {
          title: 'response.data.code 不等于 200',
          message: response.data.message,
          log: {
            api: response.config.url,
            params: response.config.data,
            data: JSON.stringify(response.data) || '{}'
          },
          time: moment(new Date()).format('YYYY-MM-DD HH:mm:ss'),
          url: w.location.hash
        })
      }
      return Promise.resolve(response)
      // return Promise.reject(response)
    } else {
      localStorage.setItem('code','200')
      return Promise.resolve(response)
    }
  },
  error => {
    if (!error.response) {
      // 服务器请求失败时错误提示
      store.dispatch('AddErrorLog', {
        title: '请求超时',
        message: ErrorMessage.API_ERROR_LOAD,
        log: {
          api: error.config.url,
          params: error.config.data,
          data: JSON.stringify(error.data) || '{}'
        },
        time: moment(new Date()).format('YYYY-MM-DD HH:mm:ss'),
        url: w.location.hash
      })
    } else {
      // 返回错误状态码
      switch (error.response.status) {
        case 400:
          error.message = ErrorMessage.STATUS_400
          break
        case 401:
          error.message = ErrorMessage.STATUS_403
          break
        case 403:
          error.message = ErrorMessage.STATUS_403
          break
        case 404:
          error.message = ErrorMessage.STATUS_404
          break
        case 408:
          error.message = ErrorMessage.STATUS_408
          break
        case 500:
          error.message = ErrorMessage.STATUS_500
          break
        case 501:
          error.message = ErrorMessage.STATUS_501
          break
        case 502:
          error.message = ErrorMessage.STATUS_502
          break
        case 503:
          error.message = ErrorMessage.STATUS_503
          break
        case 504:
          error.message = ErrorMessage.STATUS_504
          break
        case 505:
          error.message = ErrorMessage.STATUS_505
          break
        default:
      }
      store.dispatch('AddErrorLog', {
        title: '请求失败',
        message: error.message,
        log: {
          api: error.config.url,
          params: error.config.data,
          data: JSON.stringify(error.data) || '{}'
        },
        time: moment(new Date()).format('YYYY-MM-DD HH:mm:ss'),
        url: w.location.hash
      })
    }
    return Promise.reject(error)
  })

// 全局api请求封装
export default function (url, method, model, params, isPost) {
  let _config = {
    url: url,
    method: method
  }
  let _params = params ? model ? model.request(JSON.parse(JSON.stringify(params))) : params : ''
  if (method.toLowerCase() === 'post' || isPost) {
    _config.data = _params
  } else {
    _config.params = _params
  }
  return new Promise((resolve, reject) => {
    return service(_config).then(response => {
      resolve(model ? model.response(response.data) : response.data)
    }).catch(error => {
      reject(error)
    })
  })
}



