<template>
  <div>
    <div class="img-box" @click="updateState">
      <img :src="require('@/assets/image/kefu.png')" />
      <span>客服</span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      centerDialogVisible: false
    };
  },

  mounted() {},

  methods: {
    updateState() {
      window.location.href =
        "https://work.weixin.qq.com/kfid/kfcd64beafe27d5326e";
    }
  }
};
</script>

<style lang="stylus" scoped>
.img-box{
  position fixed
  right: 0px;
  bottom: 160px;
  width: 61px;
  height: 50px;
  background: #fff
  border-radius: 25px 0px 0px 25px
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 6px #CDCDCD
  z-index:9999
  img{
    width: 22px;
    height: 22px;
    margin-left: 6px
  }
  span{
    margin-left: 6px
    margin-top: 5px
    font-size: 12px;
  }
}
</style>



