// 申请退货
export function returnApplyCreate(params) {
  return axiosAPI(
    process.env.BaseUrl + '/returnApply/applyReturn?orderSn=' + params.orderSn + '&proofPics=' + params.proofPics + '&reason=' + params.reason  + '&orderItemIds=' + params.orderItemIds,
    'post',
    model.returnApplyCreateModel,
    params
  )
}

// 申请退款
export function returnApplyRefund(params) {
  let orderItemId = params.orderItemIds ? '&orderItemIds=' + params.orderItemIds : ''
  return axiosAPI(
    process.env.BaseUrl + '/returnApply/applyRefund?orderSn=' + params.orderSn + '&proofPics=' + params.proofPics + '&reason=' + params.reason + orderItemId,
    'post',
    model.returnApplyRefundModel,
    params
  )
}

