<template>
  <div class="header" @click="goBack">
    <img :src="require('@/assets/image/left.jpg')" alt=""/>
    {{name}}
  </div>
</template>

<script>

export default {
  name: 'Header',
  props: {
    name: {
      type: String,
      default: '衡磊商城'
    }
  },
  data() {
    return {
    }
  },
  methods: {
    goBack() {
      window.history.go(-1);
    }
  }
};
</script>

<style lang="stylus" scoped>
.header {
  height: 44px;
  line-height: 44px;
  background: #FFFFFF;
  color: #131313;
  font-size: 18px;
  text-align: center;
  font-weight: 500;
  img {
    height: 14px;
    left: 15px;
    position: absolute;
    top: 14px;
  }
}
</style>


